import React from "react";
import ReactTooltip from "react-tooltip";

import HullTabs from "./HullTabs.js";
import TierPane from "./TierPane.js";
import EquipmentSection from "./EquipmentSection.js";

import ShipTiers from "../../assets/json/shiptiers.json";
import ShipRoles from "../../assets/json/shiproles.json";
import TierNames from "../../assets/json/tierNames.json";
import HullTypes from "../../assets/json/hullTypes.json";
import RoleTypes from "../../assets/json/roleTypes.json";

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  Col,
  Container,
  Input,
  Modal,
  ModalBody,
  Nav,
  Row,
  TabContent,
} from "reactstrap";
import {
  Accordion,
  AccordionItem,
  AccordionItemButton,
  AccordionItemHeading,
  AccordionItemPanel,
} from "react-accessible-accordion";
import "react-accessible-accordion/dist/fancy-example.css";

function TierList() {
  const [theme] = React.useState(localStorage.getItem("theme") === "true");

  const tierlistdiv = React.useRef(null);
  const shipRoleJson = require("assets/json/shiproles.json");
  const [iconPills, setIconPills] = React.useState(0);
  const [modal, setModal] = React.useState(false);
  const [modalObj, setModalObj] = React.useState({});
  const [linkCopied, setLinkCopied] = React.useState(false);
  const [shipFilters, setShipFilters] = React.useState("");
  const [pageLoad, setPageLoad] = React.useState([
    true,
    false,
    false,
    false,
    false,
    false,
    false,
  ]);
  const [scrollElemPos, setScrollElemPos] = React.useState(1);
  const [scrollPosition, setSrollPosition] = React.useState(0);

  const tabText = [
    "BB/BC/BBV",
    "CV/CVL",
    "CB/CA",
    "CL",
    "DD",
    "SS/SSV",
    "Other",
  ];

  const openShipModal = (shipObj) => {
    const shipUncapName = shipObj.shipJson.Name.replace(/\s/g, "_");
    setModalObj({
      shipName: shipObj.shipName,
      shipImage: shipObj.shipImage,
      wikiurl: "https://azurlane.koumakan.jp/" + shipUncapName,
      shipRarity: shipObj.shipRarity,
    });
    setModal(true);
    window.history.replaceState(
      null,
      null,
      "#/home?ship=" + escape(shipUncapName.toLowerCase())
    );
  };

  const closeModal = () => {
    setModal(false);
    setLinkCopied(false);
    window.history.replaceState(null, null, "#/home");
  };

  const getRetrofitList = () => {
    let retrofits = [];
    retrofits = retrofits.concat(ShipRoles.battleShips.RETRO);
    retrofits = retrofits.concat(ShipRoles.carriers.RETRO);
    retrofits = retrofits.concat(ShipRoles.destroyers.RETRO);
    retrofits = retrofits.concat(ShipRoles.heavyCruisers.RETRO);
    retrofits = retrofits.concat(ShipRoles.lightCruisers.RETRO);
    retrofits = retrofits.concat(ShipRoles.submarines.RETRO);
    retrofits = retrofits.concat(ShipRoles.others.RETRO);
    return retrofits;
  };

  const displayReviews = (shipName) => {
    if (!shipName) return;
    try {
      const reviewJson = require("assets/json/ship_review/" + shipName);
      return (
        <Accordion allowZeroExpanded allowMultipleExpanded preExpanded={[0]}>
          {reviewJson.map((item, index) => (
            <AccordionItem key={index} uuid={index}>
              <AccordionItemHeading>
                <AccordionItemButton>{item.reviewer}</AccordionItemButton>
              </AccordionItemHeading>
              <AccordionItemPanel>
                {item.review}
                <br />
              </AccordionItemPanel>
            </AccordionItem>
          ))}
        </Accordion>
      );
    } catch (err) {
      return (
        <p>
          This ship does not need, or does not yet have, a detailed description.
        </p>
      );
    }
  };

  const getShipRarityClass = (shipJson, isRetrofit) => {
    let rarity = "rarity-" + shipJson.Rarity.toLowerCase().replace(/\s/g, "-");
    if (isRetrofit) {
      switch (rarity) {
        case "rarity-normal":
          rarity = "rarity-rare";
          break;
        case "rarity-rare":
          rarity = "rarity-elite";
          break;
        case "rarity-elite":
          rarity = "rarity-super-rare";
          break;
        case "rarity-super-rare":
          rarity = "rarity-ultra-rare";
          break;
        default:
          break;
      }
    }
    return rarity;
  };

  const copyToClipboard = (str) => {
    const el = document.createElement("textarea");
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
    setLinkCopied(true);
  };

  const handleScroll = () => {
    const position = window.pageYOffset;
    setSrollPosition(position);
  };

  React.useEffect(() => {
    setScrollElemPos(tierlistdiv.current.offsetParent.offsetTop);
    window.addEventListener("scroll", handleScroll, { passive: true });

    try {
      const urlArr = window.location.href.split("?");
      if (urlArr.length > 1) {
        let shipName = "";
        const paramArr = urlArr[1].split("&");
        for (let i = 0; i < paramArr.length; i++) {
          const paramValue = paramArr[i].split("=");
          if (paramValue[0] === "ship") {
            shipName = unescape(paramValue[1].replace(/_/g, " "));
            break;
          }
        }
        const shipJson = require("assets/json/ship/" + shipName + ".json");
        const isRetrofit = getRetrofitList().includes(shipName);
        const shipImageBanner = isRetrofit
          ? require("assets/img/ship_images/" + shipName + "kaibanner.png")
          : require("assets/img/ship_images/" + shipName + "banner.png");
        const rarity = getShipRarityClass(shipJson, isRetrofit);
        openShipModal({
          shipName: shipJson.index,
          shipImage: shipImageBanner,
          shipJson: shipJson,
          shipRarity: rarity,
        });
      }
    } catch (e) {
      console.log("Ship not found.");
      window.history.replaceState(null, null, "#/home");
    }

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div ref={tierlistdiv}>
      <Button
        className="scrollTop"
        onClick={() => {
          window.scrollTo({
            top: scrollElemPos,
            behavior: "smooth",
          });
        }}
        style={{
          padding: "8px",
          position: "fixed",
          bottom: "5px",
          right: "5px",
          zIndex: "1050",
          display: scrollElemPos < scrollPosition ? "flex" : "none",
        }}
      >
        <i className="fas fa-arrow-up"></i>
      </Button>
      <div
        className="section section-tabs"
        style={theme ? { backgroundColor: "#2F3136" } : {}}
      >
        <Container>
          <Row>
            <Col className="ml-auto mr-auto" md="12" xl="12">
              <p
                className="text-center"
                style={
                  theme
                    ? {
                        color: "#CDCDCD",
                      }
                    : { color: "#676767" }
                }
              >
                View our tiering criteria on our
                <a href="#/guidelines"> Guidelines </a>
                page!
              </p>
              <Card style={theme ? { backgroundColor: "#36393F" } : {}}>
                <CardHeader>
                  <Nav className="justify-content-center" role="tablist" tabs>
                    {HullTypes.map((tierArray, idx) => {
                      return (
                        <HullTabs
                          key={idx}
                          index={idx}
                          text={tabText[idx]}
                          iconPills={iconPills}
                          setIconPills={setIconPills}
                          setShipFilters={setShipFilters}
                          pageLoad={pageLoad}
                          setPageLoad={setPageLoad}
                        />
                      );
                    })}
                  </Nav>
                </CardHeader>
                <CardBody>
                  <TabContent
                    className="text-center"
                    activeTab={"iconPills" + iconPills}
                  >
                    {HullTypes.map((tierArray, idx) => {
                      return (
                        <TierPane
                          key={idx}
                          index={idx}
                          ShipTiers={ShipTiers}
                          ShipRoles={ShipRoles}
                          retrofitsList={getRetrofitList()}
                          TierNames={TierNames}
                          RoleTypes={RoleTypes}
                          shipRoleJson={shipRoleJson}
                          shipHull={HullTypes[idx]}
                          shipFilters={shipFilters}
                          setShipFilters={setShipFilters}
                          openShipModal={openShipModal}
                          willLoadPage={pageLoad[idx]}
                          ReactTooltip={ReactTooltip}
                        />
                      );
                    })}
                  </TabContent>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>

      <Modal size="lg" isOpen={modal} toggle={() => closeModal()}>
        <div style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}>
          <div className="modal-header justify-content-center">
            <button
              className="close"
              type="button"
              onClick={() => closeModal()}
            >
              <i className="now-ui-icons ui-1_simple-remove"></i>
            </button>
            <div style={{ textAlign: "center" }}>
              <img
                alt="..."
                className={
                  "rounded img-raised ship-rarity-padding " +
                  modalObj.shipRarity
                }
                src={modalObj.shipImage}
              ></img>
              <br />
              <h4
                className="title title-up hover-link"
                style={{ cursor: "pointer" }}
                onClick={(e) => {
                  window.open(modalObj.wikiurl, "_blank");
                }}
              >
                {modalObj.shipName}
              </h4>
              <hr />
            </div>
          </div>
          <ModalBody>
            <EquipmentSection
              shipName={modalObj.shipName}
              isRetrofit={getRetrofitList().includes(modalObj.shipName)}
              rarity={modalObj.shipRarity}
            />
            <hr />
            {displayReviews(modalObj.shipName)}

            <hr />
            <Row>
              <Col md="12" className="text-center">
                Share this ship's details:
                <Button
                  onClick={() => copyToClipboard(window.location.href)}
                  className="btn-link"
                  color="info"
                >
                  {linkCopied ? "Link copied!" : "Click here to copy link!"}
                </Button>
              </Col>
              <Col xs="12" sm="12" md={{ size: 6, offset: 3 }}>
                <Input
                  id="ship_link_tb"
                  defaultValue={window.location.href}
                  style={{
                    cursor: "text",
                  }}
                  placeholder="Regular"
                  type="text"
                  disabled={true}
                ></Input>
              </Col>
            </Row>
          </ModalBody>
          <div className="modal-footer">
            <Button
              color="default"
              type="button"
              onClick={(e) => {
                window.open(modalObj.wikiurl, "_blank");
              }}
            >
              View on wiki
            </Button>
            <Button color="danger" type="button" onClick={() => closeModal()}>
              Close
            </Button>
          </div>
        </div>
      </Modal>
      <ReactTooltip style={{ maxWidth: "300px" }} />
    </div>
  );
}

export default TierList;
