import React, { Component } from "react";
import { NavItem, NavLink } from "reactstrap";

export default class HullTabs extends Component {
  render() {
    const index = this.props.index;
    const text = this.props.text;
    const iconPills = this.props.iconPills;
    const setIconPills = this.props.setIconPills;
    const setShipFilters = this.props.setShipFilters;
    const pageLoad = this.props.pageLoad;
    const setPageLoad = this.props.setPageLoad;

    const updatePageLoad = () => {
      let pageLoadArr = pageLoad.slice();
      if (!pageLoadArr[index]) {
        pageLoadArr[index] = true;
        setPageLoad(pageLoadArr);
      }
    };

    return (
      <NavItem sm="6" md="2">
        <NavLink
          className={iconPills === index ? "active" : ""}
          href="#pablo"
          onClick={(e) => {
            e.preventDefault();
            setIconPills(index);
            setShipFilters("");
            updatePageLoad();
          }}
        >
          {text}
        </NavLink>
      </NavItem>
    );
  }
}
