import React, { Component } from "react";

import { Row, Col, Label, Input } from "reactstrap";

export default class FilterBar extends Component {
  constructor(props) {
    super(props);
    this.state = { theme: localStorage.getItem("theme") === "true" };
  }
  render() {
    const shipHull = this.props.shipHull;
    const RoleTypes = this.props.RoleTypes;
    const shipRoleList = this.props.shipRoleList[shipHull];
    const shipFilters = this.props.shipFilters;
    const setShipFilters = this.props.setShipFilters;

    const getItemMarkup = () => {
      let filterItemList = [];
      for (let i = 0; i < RoleTypes.length; i++) {
        if (RoleTypes[i].code.indexOf("_INVERSE") > -1) {
          let invFilterCode = RoleTypes[i].code;
          invFilterCode = invFilterCode.substr(
            0,
            invFilterCode.indexOf("_INVERSE")
          );
          if (shipRoleList[invFilterCode].length === 0) {
            continue;
          }
        }
        if (
          RoleTypes[i].code.indexOf("_INVERSE") > -1 ||
          shipRoleList[RoleTypes[i].code].length !== 0
        ) {
          filterItemList.push(
            <Col sm="6" md="2" key={i}>
              <Label check>
                <Input
                  name={shipHull}
                  type="radio"
                  checked={shipFilters === RoleTypes[i].code}
                  onChange={() => {
                    setShipFilters(RoleTypes[i].code);
                  }}
                ></Input>
                <span
                  className="form-check-sign"
                  style={this.state.theme ? { color: "white" } : {}}
                >
                  {RoleTypes[i].filter_label} &nbsp;
                </span>
                <img
                  key={i}
                  alt="..."
                  data-tip={RoleTypes[i].tip}
                  style={{ maxHeight: "20px" }}
                  src={require("assets/img/icons/" + RoleTypes[i].img)}
                ></img>
              </Label>
            </Col>
          );
        }
      }

      return filterItemList;
    };

    return (
      <>
        <Row>
          {
            <Col sm="6" md="2">
              <Label check>
                <Input
                  name={shipHull}
                  type="radio"
                  checked={shipFilters === ""}
                  onChange={() => {
                    setShipFilters("");
                  }}
                ></Input>
                <span
                  className="form-check-sign"
                  style={this.state.theme ? { color: "white" } : {}}
                >
                  All Ships
                </span>
              </Label>
            </Col>
          }
          {getItemMarkup()}
        </Row>
      </>
    );
  }
}
