import React, { Component } from "react";
import { Row, Col, Button, UncontrolledTooltip } from "reactstrap";

import gearMapping from "../../assets/json/gear/GearMapping.json";
import bbTemplates from "../../assets/json/gear/BBTemplates.json";
import caTemplates from "../../assets/json/gear/CATemplates.json";
import clTemplates from "../../assets/json/gear/CLTemplates.json";
import cvTemplates from "../../assets/json/gear/CVTemplates.json";
import ddTemplates from "../../assets/json/gear/DDTemplates.json";
import ssTemplates from "../../assets/json/gear/SSTemplates.json";
import otherTemplates from "../../assets/json/gear/OtherTemplates.json";
import slotTemplates from "../../assets/json/gear/SlotTemplates.json";

const hullTemplates = {
  ...bbTemplates,
  ...caTemplates,
  ...clTemplates,
  ...cvTemplates,
  ...ddTemplates,
  ...ssTemplates,
  ...otherTemplates,
};

export default class EquipmentSection extends Component {
  constructor(props) {
    super(props);

    try {
      const shipName = this.props.shipName;
      const isRetrofit = this.props.isRetrofit;
      const shipRarity = this.props.rarity;
      const slotMap = hullTemplates[gearMapping[shipName]];
      const templateForShip = {
        slot_1: slotTemplates[slotMap["slot_1"]],
        slot_2: slotTemplates[slotMap["slot_2"]],
        slot_3: slotTemplates[slotMap["slot_3"]],
        slot_4: slotTemplates[slotMap["slot_4"]],
        slot_5: slotTemplates[slotMap["slot_5"]],
      };
      if (templateForShip) {
        const shipData = require("../../assets/json/ship/" +
          shipName +
          ".json");
        this.state = {
          eqData: templateForShip,
          shipData: shipData,
          isRetrofit: isRetrofit,
          shipRarity: shipRarity,
        };
      }
    } catch (e) {
      console.log(e);
      console.log("Unable to link ship with equipment.");
    }
  }

  render() {
    const getShipRarityFromClass = (className) => {
      switch (className) {
        case "rarity-ultra-rare":
        case "rarity-decisive":
          return "ur";
        case "rarity-super-rare":
        case "rarity-priority":
          return "#ee9";
        case "rarity-elite":
          return "#c4adff";
        case "rarity-rare":
          return "#9fe8ff";
        case "rarity-normal":
          return "#ccc";
        default:
          return "#ccc";
      }
    };

    const getRarityColor = (gear, isColorCode = false) => {
      try {
        switch (require("../../assets/json/eq_json/" + gear + ".json").Stars) {
          case "2":
            return isColorCode ? "#ccc" : "rarity-normal";
          case "3":
            return isColorCode ? "#9fe8ff" : "rarity-rare";
          case "4":
            return isColorCode ? "#c4adff" : "rarity-elite";
          case "5":
            return isColorCode ? "#ee9" : "rarity-super-rare";
          case "6":
            return isColorCode ? "ur" : "rarity-ultra-rare";
          default:
            return isColorCode ? "#ccc" : "rarity-normal";
        }
      } catch (e) {
        return "rarity-normal";
      }
    };

    const getGearAcq = (gear) => {
      try {
        let gearLoc = require("../../assets/json/eq_json/" + gear + ".json")
          .DropLocation;
        while (gearLoc.indexOf("[[") > -1) {
          let tempStr = gearLoc.substr(gearLoc.indexOf("[[") + 2);
          tempStr = tempStr.substr(0, tempStr.indexOf("]]"));
          if (tempStr.indexOf("|") === -1) {
            gearLoc = gearLoc.replace("[[", "");
            gearLoc = gearLoc.replace("]]", "");
          } else {
            gearLoc =
              gearLoc.substr(0, gearLoc.indexOf("[[")) +
              tempStr.split("|")[1] +
              gearLoc.substr(gearLoc.indexOf("]]") + 2);
          }
        }
        return gearLoc
          .replace(/&lt;br\/&gt;/g, ",\n")
          .replace(/&lt;br&gt;/g, ",\n")
          .replace(/&quot;/g, '"')
          .replace(/25px/g, "points");
      } catch (e) {
        return "-";
      }
    };

    const getGearName = (gear) => {
      try {
        return require("../../assets/json/eq_json/" +
          gear +
          ".json").Name.replace(/&quot;/g, '"');
      } catch (e) {
        return "-";
      }
    };

    const openGearInWiki = (gear) => {
      try {
        const gearJson = require("../../assets/json/eq_json/" + gear + ".json");
        window.open(
          "https://azurlane.koumakan.jp/" +
            gearJson.Name.replace(/&quot;/g, '"').replace(/ /g, "_") +
            (gearJson.Tech === "T3" ? "#Type_3" : ""),
          "_blank"
        );
      } catch (e) {
        alert("Unable to find the link for this gear! :(");
      }
    };

    const downloadEqImage = async () => {
      let maxSlot = 0;
      let maxItem = 0;
      maxItem += this.state.eqData.slot_1.length;
      if (this.state.eqData.slot_1.length > maxSlot)
        maxSlot = this.state.eqData.slot_1.length;
      maxItem += this.state.eqData.slot_2.length;
      if (this.state.eqData.slot_2.length > maxSlot)
        maxSlot = this.state.eqData.slot_2.length;
      maxItem += this.state.eqData.slot_3.length;
      if (this.state.eqData.slot_3.length > maxSlot)
        maxSlot = this.state.eqData.slot_3.length;
      maxItem += this.state.eqData.slot_4.length;
      if (this.state.eqData.slot_4.length > maxSlot)
        maxSlot = this.state.eqData.slot_4.length;
      maxItem += this.state.eqData.slot_5.length;
      if (this.state.eqData.slot_5.length > maxSlot)
        maxSlot = this.state.eqData.slot_5.length;

      const canvasWidth = 800;
      const canvasHeight = 200 + maxSlot * 115 + 30 + maxItem * 50 + 50;

      const { createCanvas, loadImage } = require("canvas");
      const canvas = createCanvas(canvasWidth, canvasHeight);
      const ctx = canvas.getContext("2d");

      const shipName = this.props.shipName;
      const rainbowbg = await loadImage(
        require("../../assets/img/others/rainbowbg.png")
      );

      ctx.fillStyle = "#31394D";
      ctx.fillRect(0, 0, canvasWidth, canvasHeight);

      // SHIP IMAGE
      const shipRarityCode = getShipRarityFromClass(this.state.shipRarity);
      if (shipRarityCode !== "ur") {
        ctx.fillStyle = shipRarityCode;
        ctx.fillRect(175, 20, 450, 100);
      } else {
        ctx.drawImage(rainbowbg, 175, 20, 450, 100);
      }

      let shipImage = "";
      if (this.state.isRetrofit) {
        shipImage = await loadImage(
          require("assets/img/ship_images/" + shipName + "kaibanner.png")
        );
      } else {
        shipImage = await loadImage(
          require("assets/img/ship_images/" + shipName + "banner.png")
        );
      }
      ctx.drawImage(shipImage, 175, 20, 450, 100);

      // SHIP NAME
      ctx.fillStyle = "#FFFFFF";
      ctx.font = "bold 16px Arial";
      ctx.textAlign = "center";
      ctx.fillText(shipName.toUpperCase(), 400, 145);

      // HORIZONTAL RULE
      ctx.beginPath();
      ctx.strokeStyle = "#FFFFFF";
      ctx.moveTo(25, 160);
      ctx.lineTo(775, 160);
      ctx.stroke();

      const heightAfterPic = 200 + maxSlot * 115 + 40;
      let itemCount = 0;

      let equippableText1 = this.state.shipData.Eq1Type.toUpperCase();
      console.log(shipName);
      if (equippableText1.indexOf("(") > -1) {
        equippableText1 = equippableText1.substr(
          0,
          equippableText1.indexOf("(")
        );
      }
      if (shipName === "shinano") {
        equippableText1 = "ALL PLANES";
      }
      let equippableText2 = this.state.shipData.Eq2Type.toUpperCase();
      if (equippableText2.indexOf("(") > -1) {
        equippableText2 = equippableText2.substr(
          0,
          equippableText2.indexOf("(")
        );
      }
      let equippableText3 = this.state.shipData.Eq3Type.toUpperCase();
      if (equippableText3.indexOf("(") > -1) {
        equippableText3 = equippableText3.substr(
          0,
          equippableText3.indexOf("(")
        );
      }

      ctx.fillStyle = "#FFFFFF";
      ctx.font = "normal 11px Arial";
      ctx.textAlign = "center";
      ctx.fillText(equippableText1, 150, 180);
      ctx.fillText(equippableText2, 275, 180);
      ctx.fillText(equippableText3, 400, 180);

      if (this.state.shipData.Eq1Type !== "Auxiliary Equipment") {
        ctx.fillText(
          this.state.isRetrofit
            ? this.state.shipData.Eq1EffInitKai
            : this.state.shipData.Eq1EffInitMax + " eff",
          150,
          190
        );
      }
      if (this.state.shipData.Eq2Type !== "Auxiliary Equipment") {
        ctx.fillText(
          this.state.isRetrofit
            ? this.state.shipData.Eq2EffInitKai
            : this.state.shipData.Eq2EffInitMax + " eff",
          275,
          190
        );
      }
      if (this.state.shipData.Eq3Type !== "Auxiliary Equipment") {
        ctx.fillText(
          this.state.isRetrofit
            ? this.state.shipData.Eq3EffInitKai
            : this.state.shipData.Eq3EffInitMax + " eff",
          400,
          190
        );
      }

      for (let i = 0; i < this.state.eqData.slot_1.length; i++) {
        const rarityColor = getRarityColor(
          this.state.eqData.slot_1[i].gear,
          true
        );
        if (rarityColor !== "ur") {
          ctx.fillStyle = rarityColor;
          ctx.fillRect(100, 200 + i * 115, 100, 100);
          ctx.fillRect(25, heightAfterPic + itemCount * 50, 40, 40);
        } else {
          ctx.drawImage(rainbowbg, 100, 200 + i * 115, 100, 100);
          ctx.drawImage(rainbowbg, 25, heightAfterPic + itemCount * 50, 40, 40);
        }

        const eqImage = await loadImage(
          require("../../assets/img/eq_images/" +
            this.state.eqData.slot_1[i].gear +
            ".png")
        );
        ctx.drawImage(eqImage, 100, 200 + i * 115, 100, 100);
        ctx.drawImage(eqImage, 25, heightAfterPic + itemCount * 50, 40, 40);

        ctx.fillStyle = "#FFFFFF";
        ctx.font = "normal 14px Arial";
        ctx.textAlign = "left";

        let tooltipStr = this.state.eqData.slot_1[i].tooltip;
        let textHeight = heightAfterPic + itemCount * 50 + 25;
        if (tooltipStr.length > 110) {
          const splitIndex = tooltipStr.substr(0, 110).lastIndexOf(" ");
          ctx.fillText(
            tooltipStr.substr(splitIndex + 1),
            70,
            heightAfterPic + itemCount * 50 + 35
          );

          textHeight = heightAfterPic + itemCount * 50 + 15;
          tooltipStr = tooltipStr.substr(0, splitIndex);
        }
        ctx.fillText(tooltipStr, 70, textHeight);
        itemCount++;
      }

      for (let i = 0; i < this.state.eqData.slot_2.length; i++) {
        const rarityColor = getRarityColor(
          this.state.eqData.slot_2[i].gear,
          true
        );
        if (rarityColor !== "ur") {
          ctx.fillStyle = rarityColor;
          ctx.fillRect(225, 200 + i * 115, 100, 100);
          ctx.fillRect(25, heightAfterPic + itemCount * 50, 40, 40);
        } else {
          ctx.drawImage(rainbowbg, 225, 200 + i * 115, 100, 100);
          ctx.drawImage(rainbowbg, 25, heightAfterPic + itemCount * 50, 40, 40);
        }

        const eqImage = await loadImage(
          require("../../assets/img/eq_images/" +
            this.state.eqData.slot_2[i].gear +
            ".png")
        );
        ctx.drawImage(eqImage, 225, 200 + i * 115, 100, 100);
        ctx.drawImage(eqImage, 25, heightAfterPic + itemCount * 50, 40, 40);

        ctx.fillStyle = "#FFFFFF";
        ctx.font = "normal 14px Arial";
        ctx.textAlign = "left";

        let tooltipStr = this.state.eqData.slot_2[i].tooltip;
        let textHeight = heightAfterPic + itemCount * 50 + 25;
        if (tooltipStr.length > 110) {
          const splitIndex = tooltipStr.substr(0, 110).lastIndexOf(" ");
          ctx.fillText(
            tooltipStr.substr(splitIndex + 1),
            70,
            heightAfterPic + itemCount * 50 + 35
          );

          textHeight = heightAfterPic + itemCount * 50 + 15;
          tooltipStr = tooltipStr.substr(0, splitIndex);
        }
        ctx.fillText(tooltipStr, 70, textHeight);
        itemCount++;
      }

      for (let i = 0; i < this.state.eqData.slot_3.length; i++) {
        const rarityColor = getRarityColor(
          this.state.eqData.slot_3[i].gear,
          true
        );
        if (rarityColor !== "ur") {
          ctx.fillStyle = rarityColor;
          ctx.fillRect(350, 200 + i * 115, 100, 100);
          ctx.fillRect(25, heightAfterPic + itemCount * 50, 40, 40);
        } else {
          ctx.drawImage(rainbowbg, 350, 200 + i * 115, 100, 100);
          ctx.drawImage(rainbowbg, 25, heightAfterPic + itemCount * 50, 40, 40);
        }

        const eqImage = await loadImage(
          require("../../assets/img/eq_images/" +
            this.state.eqData.slot_3[i].gear +
            ".png")
        );
        ctx.drawImage(eqImage, 350, 200 + i * 115, 100, 100);
        ctx.drawImage(eqImage, 25, heightAfterPic + itemCount * 50, 40, 40);

        ctx.fillStyle = "#FFFFFF";
        ctx.font = "normal 14px Arial";
        ctx.textAlign = "left";

        let tooltipStr = this.state.eqData.slot_3[i].tooltip;
        let textHeight = heightAfterPic + itemCount * 50 + 25;
        if (tooltipStr.length > 110) {
          const splitIndex = tooltipStr.substr(0, 110).lastIndexOf(" ");
          ctx.fillText(
            tooltipStr.substr(splitIndex + 1),
            70,
            heightAfterPic + itemCount * 50 + 35
          );

          textHeight = heightAfterPic + itemCount * 50 + 15;
          tooltipStr = tooltipStr.substr(0, splitIndex);
        }
        ctx.fillText(tooltipStr, 70, textHeight);
        itemCount++;
      }

      for (let i = 0; i < this.state.eqData.slot_4.length; i++) {
        const rarityColor = getRarityColor(
          this.state.eqData.slot_4[i].gear,
          true
        );
        if (rarityColor !== "ur") {
          ctx.fillStyle = rarityColor;
          ctx.fillRect(475, 200 + i * 115, 100, 100);
          ctx.fillRect(25, heightAfterPic + itemCount * 50, 40, 40);
        } else {
          ctx.drawImage(rainbowbg, 475, 200 + i * 115, 100, 100);
          ctx.drawImage(rainbowbg, 25, heightAfterPic + itemCount * 50, 40, 40);
        }

        const eqImage = await loadImage(
          require("../../assets/img/eq_images/" +
            this.state.eqData.slot_4[i].gear +
            ".png")
        );
        ctx.drawImage(eqImage, 475, 200 + i * 115, 100, 100);
        ctx.drawImage(eqImage, 25, heightAfterPic + itemCount * 50, 40, 40);

        ctx.fillStyle = "#FFFFFF";
        ctx.font = "normal 14px Arial";
        ctx.textAlign = "left";

        let tooltipStr = this.state.eqData.slot_4[i].tooltip;
        let textHeight = heightAfterPic + itemCount * 50 + 25;
        if (tooltipStr.length > 110) {
          const splitIndex = tooltipStr.substr(0, 110).lastIndexOf(" ");
          ctx.fillText(
            tooltipStr.substr(splitIndex + 1),
            70,
            heightAfterPic + itemCount * 50 + 35
          );

          textHeight = heightAfterPic + itemCount * 50 + 15;
          tooltipStr = tooltipStr.substr(0, splitIndex);
        }
        ctx.fillText(tooltipStr, 70, textHeight);
        itemCount++;
      }

      for (let i = 0; i < this.state.eqData.slot_5.length; i++) {
        const rarityColor = getRarityColor(
          this.state.eqData.slot_5[i].gear,
          true
        );
        if (rarityColor !== "ur") {
          ctx.fillStyle = rarityColor;
          ctx.fillRect(600, 200 + i * 115, 100, 100);
          ctx.fillRect(25, heightAfterPic + itemCount * 50, 40, 40);
        } else {
          ctx.drawImage(rainbowbg, 600, 200 + i * 115, 100, 100);
          ctx.drawImage(rainbowbg, 25, heightAfterPic + itemCount * 50, 40, 40);
        }

        const eqImage = await loadImage(
          require("../../assets/img/eq_images/" +
            this.state.eqData.slot_5[i].gear +
            ".png")
        );
        ctx.drawImage(eqImage, 600, 200 + i * 115, 100, 100);
        ctx.drawImage(eqImage, 25, heightAfterPic + itemCount * 50, 40, 40);

        ctx.fillStyle = "#FFFFFF";
        ctx.font = "normal 14px Arial";
        ctx.textAlign = "left";

        let tooltipStr = this.state.eqData.slot_5[i].tooltip;
        let textHeight = heightAfterPic + itemCount * 50 + 25;
        if (tooltipStr.length > 110) {
          const splitIndex = tooltipStr.substr(0, 110).lastIndexOf(" ");

          ctx.fillText(
            tooltipStr.substr(splitIndex + 1),
            70,
            heightAfterPic + itemCount * 50 + 35
          );

          textHeight = heightAfterPic + itemCount * 50 + 15;
          tooltipStr = tooltipStr.substr(0, splitIndex);
        }
        ctx.fillText(tooltipStr, 70, textHeight);
        itemCount++;
      }

      // HORIZONTAL RULE
      ctx.beginPath();
      ctx.strokeStyle = "#FFFFFF";
      ctx.moveTo(25, 200 + maxSlot * 115 + 20);
      ctx.lineTo(775, 200 + maxSlot * 115 + 20);
      ctx.stroke();

      //      100-200 225-325 350-450 475-575 600-700

      ctx.fillStyle = "#FFFFFF";
      ctx.font = "normal 11px Arial";
      ctx.textAlign = "left";
      ctx.fillText(
        "By -It's Rajaku-#1817, Nerezza#7031, Slime#2203, Rice#7016",
        5,
        heightAfterPic + itemCount * 50 + 35
      );

      ctx.textAlign = "right";
      ctx.fillText(
        "For more, visit: https://slaimuda.github.io/ectl",
        795,
        heightAfterPic + itemCount * 50 + 35
      );

      ctx.textAlign = "right";
      ctx.fillText(
        "Azur Lane Community Tier List",
        795,
        heightAfterPic + itemCount * 50 + 25
      );

      ctx.textAlign = "right";
      ctx.fillText("Generated on " + new Date().toGMTString(), 795, 10);

      let a = document.createElement("a");
      a.href = canvas.toDataURL();
      a.download = shipName.toLowerCase() + ".png";
      a.click();
    };

    return !this.state ? (
      ""
    ) : (
      <>
        <Row>
          <Col xs="4" sm="4" md={{ size: "2", offset: "1" }}>
            <Row className="text-center">
              <h6
                style={{
                  width: "100%",
                  maxHeight: "15px",
                  marginBottom: "45px",
                }}
              >
                {this.state.shipData.Eq1Type}
              </h6>
              <h6 style={{ width: "100%" }}>
                {this.state.shipData.Eq1Type === "Auxiliary Equipment" ? (
                  <span>&nbsp;</span>
                ) : this.state.isRetrofit ? (
                  this.state.shipData.Eq1EffInitKai + " eff"
                ) : (
                  this.state.shipData.Eq1EffInitMax + " eff"
                )}
              </h6>
              {this.state.eqData.slot_1.map((item, index) => (
                <Col md="12" key={index}>
                  <img
                    id={"_" + item.gear + "_" + index + "1"}
                    alt="..."
                    className={
                      "rounded img-raised eq-rarity-padding " +
                      getRarityColor(item.gear)
                    }
                    src={require("../../assets/img/eq_images/" +
                      item.gear +
                      ".png")}
                    style={{
                      maxWidth: "128px",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    onDoubleClick={() => openGearInWiki(item.gear)}
                  ></img>
                  <UncontrolledTooltip
                    delay={0}
                    placement="auto"
                    className="popover-default"
                    target={"_" + item.gear + "_" + index + "1"}
                  >
                    <h6>{getGearName(item.gear)}</h6>
                    <hr />
                    {item.tooltip}
                    <hr />
                    {getGearAcq(item.gear)}
                  </UncontrolledTooltip>
                </Col>
              ))}
            </Row>
            <hr style={{ width: "100%" }} />
          </Col>
          <Col xs="4" sm="4" md="2">
            <Row className="text-center">
              <h6
                style={{
                  width: "100%",
                  maxHeight: "15px",
                  marginBottom: "45px",
                }}
              >
                {this.state.shipData.Eq2Type}
              </h6>
              <h6 style={{ width: "100%" }}>
                {this.state.shipData.Eq2Type === "Auxiliary Equipment" ? (
                  <span>&nbsp;</span>
                ) : this.state.isRetrofit ? (
                  this.state.shipData.Eq2EffInitKai + " eff"
                ) : (
                  this.state.shipData.Eq2EffInitMax + " eff"
                )}{" "}
              </h6>
              {this.state.eqData.slot_2.map((item, index) => (
                <Col md="12" key={index}>
                  <img
                    id={"_" + item.gear + "_" + index + "2"}
                    alt="..."
                    className={
                      "rounded img-raised eq-rarity-padding " +
                      getRarityColor(item.gear)
                    }
                    src={require("../../assets/img/eq_images/" +
                      item.gear +
                      ".png")}
                    style={{
                      maxWidth: "128px",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    onDoubleClick={() => openGearInWiki(item.gear)}
                  ></img>
                  <UncontrolledTooltip
                    delay={0}
                    placement="auto"
                    className="popover-default"
                    target={"_" + item.gear + "_" + index + "2"}
                  >
                    <h6>{getGearName(item.gear)}</h6>
                    <hr />
                    {item.tooltip}
                    <hr />
                    {getGearAcq(item.gear)}
                  </UncontrolledTooltip>
                </Col>
              ))}
            </Row>
            <hr style={{ width: "100%" }} />
          </Col>
          <Col xs="4" sm="4" md="2">
            <Row className="text-center">
              <h6
                style={{
                  width: "100%",
                  maxHeight: "15px",
                  marginBottom: "45px",
                }}
              >
                {this.state.shipData.Eq3Type}
              </h6>
              <h6 style={{ width: "100%" }}>
                {this.state.shipData.Eq3Type === "Auxiliary Equipment" ? (
                  <span>&nbsp;</span>
                ) : this.state.isRetrofit ? (
                  this.state.shipData.Eq3EffInitKai + " eff"
                ) : (
                  this.state.shipData.Eq3EffInitMax + " eff"
                )}{" "}
              </h6>
              {this.state.eqData.slot_3.map((item, index) => (
                <Col md="12" key={index}>
                  <img
                    id={"_" + item.gear + "_" + index + "3"}
                    alt="..."
                    className={
                      "rounded img-raised eq-rarity-padding " +
                      getRarityColor(item.gear)
                    }
                    src={require("../../assets/img/eq_images/" +
                      item.gear +
                      ".png")}
                    style={{
                      maxWidth: "128px",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    onDoubleClick={() => openGearInWiki(item.gear)}
                  ></img>
                  <UncontrolledTooltip
                    delay={0}
                    placement="auto"
                    className="popover-default"
                    target={"_" + item.gear + "_" + index + "3"}
                  >
                    <h6>{getGearName(item.gear)}</h6>
                    <hr />
                    {item.tooltip}
                    <hr />
                    {getGearAcq(item.gear)}
                  </UncontrolledTooltip>
                </Col>
              ))}
            </Row>
            <hr style={{ width: "100%" }} />
          </Col>
          <Col xs="4" sm="4" md="2">
            <Row className="text-center">
              <h6
                style={{
                  width: "100%",
                  maxHeight: "15px",
                  marginBottom: "45px",
                }}
              >
                Auxiliary 1
              </h6>
              <h6 style={{ width: "100%" }}>&nbsp;</h6>
              {this.state.eqData.slot_4.map((item, index) => (
                <Col md="12" key={index}>
                  <img
                    id={"_" + item.gear + "_" + index + "4"}
                    alt="..."
                    className={
                      "rounded img-raised eq-rarity-padding " +
                      getRarityColor(item.gear)
                    }
                    src={require("../../assets/img/eq_images/" +
                      item.gear +
                      ".png")}
                    style={{
                      maxWidth: "128px",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    onDoubleClick={() => openGearInWiki(item.gear)}
                  ></img>
                  <UncontrolledTooltip
                    delay={0}
                    placement="auto"
                    className="popover-default"
                    target={"_" + item.gear + "_" + index + "4"}
                  >
                    <h6>{getGearName(item.gear)}</h6>
                    <hr />
                    {item.tooltip}
                    <hr />
                    {getGearAcq(item.gear)}
                  </UncontrolledTooltip>
                </Col>
              ))}
            </Row>
            <hr style={{ width: "100%" }} />
          </Col>
          <Col xs="4" sm="4" md="2">
            <Row className="text-center">
              <h6
                style={{
                  width: "100%",
                  maxHeight: "15px",
                  marginBottom: "45px",
                }}
              >
                Auxiliary 2
              </h6>
              <h6 style={{ width: "100%" }}>&nbsp;</h6>
              {this.state.eqData.slot_5.map((item, index) => (
                <Col md="12" key={index}>
                  <img
                    id={"_" + item.gear + "_" + index + "5"}
                    alt="..."
                    className={
                      "rounded img-raised eq-rarity-padding " +
                      getRarityColor(item.gear)
                    }
                    src={require("../../assets/img/eq_images/" +
                      item.gear +
                      ".png")}
                    style={{
                      maxWidth: "128px",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    onDoubleClick={() => openGearInWiki(item.gear)}
                  ></img>
                  <UncontrolledTooltip
                    delay={0}
                    placement="auto"
                    className="popover-info"
                    target={"_" + item.gear + "_" + index + "5"}
                  >
                    <h6>{getGearName(item.gear)}</h6>
                    <hr />
                    {item.tooltip}
                    <hr />
                    {getGearAcq(item.gear)}
                  </UncontrolledTooltip>
                </Col>
              ))}
            </Row>
            <hr style={{ width: "100%" }} />
          </Col>
          <span
            xs="12"
            sm="12"
            md="0"
            style={{ textAlign: "center", width: "100%" }}
          >
            Double click equipment icon to view in Wiki
            <br />
            <Button
              onClick={() => downloadEqImage()}
              className="btn-link"
              color="info"
            >
              Download as an image
            </Button>
          </span>
        </Row>
      </>
    );
  }
}
