import React, { forwardRef, useImperativeHandle } from "react";
import { Col } from "reactstrap";
import { Dropdown, Checkbox } from "semantic-ui-react";
import "semantic-ui-css/semantic.min.css";

let eqlist = [];
let updateshipjson;
let bisGearTable = {};

const ShipSelector = forwardRef((props, ref) => {
  const [theme, setTheme] = React.useState();
  const [selectorid, setSelectorid] = React.useState([]);
  const [shipFeedbackJson, setShipFeedbackJson] = React.useState({});
  const [shipList, setShipList] = React.useState([]);

  const [shipValue, setShipValue] = React.useState(0);
  const [shipHasKai, setShipHasKai] = React.useState(false);
  const [shipKai, setShipKai] = React.useState(false);

  const [equipment1List, setEquipment1List] = React.useState([]);
  const [equipment2List, setEquipment2List] = React.useState([]);
  const [equipment3List, setEquipment3List] = React.useState([]);
  const [equipment4List, setEquipment4List] = React.useState([]);
  const [equipment5List, setEquipment5List] = React.useState([]);

  const [equipment1, setEquipment1] = React.useState(0);
  const [equipment2, setEquipment2] = React.useState(0);
  const [equipment3, setEquipment3] = React.useState(0);
  const [equipment4, setEquipment4] = React.useState(0);
  const [equipment5, setEquipment5] = React.useState(0);

  React.useEffect(() => {
    setTheme(props.theme);
    const tempShipList = props.shiplist;
    setShipList(tempShipList);
    eqlist = props.eqlist;
    updateshipjson = props.updateshipjson;
    bisGearTable = props.bisGearTable;
    setSelectorid(props.selectorid);
    if (props.updateJson !== undefined) {
      for (let i = 0; i < tempShipList.length; i++) {
        if (tempShipList[i].file === props.updateJson.ship) {
          props.updateJson.selectorid = props.selectorid;
          setShipEquipment(i, props.updateJson);
          setShipValue(i);
        }
      }
    }

    // eslint-disable-next-line
  }, []);

  useImperativeHandle(ref, () => ({
    clearShip: () => {
      setShipValue(0);
      setShipHasKai(0);
      setShipHasKai(0);
      setEquipment1List([]);
      setEquipment2List([]);
      setEquipment3List([]);
      setEquipment4List([]);
      setEquipment5List([]);
      setEquipment1(0);
      setEquipment2(0);
      setEquipment3(0);
      setEquipment4(0);
      setEquipment5(0);
    },
  }));

  const toggleKai = (isKai) => {
    let tempjson = updateShipFeedbackJson(isKai, "kai");

    const ship_json = require("../../assets/json/ship/" +
      shipList[shipValue].file +
      ".json");
    const shipHullName = ship_json.Type;
    const eq1 = ship_json.Eq1Type;
    const eq2 = ship_json.Eq2Type;
    const eq3 = ship_json.Eq3Type;

    const equippableArr1 = getEquipmentListFromEquippable(
      eq1,
      shipHullName,
      isKai
    );
    const equippableArr2 = getEquipmentListFromEquippable(
      eq2,
      shipHullName,
      isKai
    );
    const equippableArr3 = getEquipmentListFromEquippable(
      eq3,
      shipHullName,
      isKai
    );
    if (JSON.stringify(equippableArr1) !== JSON.stringify(equipment1List)) {
      setEquipment1List(equippableArr1);
      setEquipment1(0);
      tempjson = updateShipFeedbackJson(
        equippableArr1[0].file,
        "eq1",
        tempjson
      );
    }
    if (JSON.stringify(equippableArr2) !== JSON.stringify(equipment2List)) {
      setEquipment2List(equippableArr2);
      setEquipment2(0);
      tempjson = updateShipFeedbackJson(
        equippableArr2[0].file,
        "eq2",
        tempjson
      );
    }
    if (JSON.stringify(equippableArr3) !== JSON.stringify(equipment3List)) {
      setEquipment3List(equippableArr3);
      setEquipment3(0);
      tempjson = updateShipFeedbackJson(
        equippableArr3[0].file,
        "eq3",
        tempjson
      );
    }
    updateshipjson(tempjson, selectorid);
  };

  const setShipEquipment = (shipIndex, updateJson = null) => {
    if (shipIndex === 0) {
      return;
    }
    const ship_json = updateJson
      ? require("../../assets/json/ship/" + updateJson.ship + ".json")
      : require("../../assets/json/ship/" + shipList[shipIndex].file + ".json");
    const shipHullName = ship_json.Type;
    let shipHasKai = checkShipKai(shipIndex, updateJson);
    const eq1 = ship_json.Eq1Type;
    const eq2 = ship_json.Eq2Type;
    const eq3 = ship_json.Eq3Type;
    const equippableArr1 = getEquipmentListFromEquippable(
      eq1,
      shipHullName,
      shipHasKai
    );
    const equippableArr2 = getEquipmentListFromEquippable(
      eq2,
      shipHullName,
      shipHasKai
    );
    const equippableArr3 = getEquipmentListFromEquippable(
      eq3,
      shipHullName,
      shipHasKai
    );
    const equippableAuxArr = filterAuxByShipType(shipHullName);
    setEquipment1List(equippableArr1);
    setEquipment2List(equippableArr2);
    setEquipment3List(equippableArr3);
    setEquipment4List(equippableAuxArr);
    setEquipment5List(equippableAuxArr);
    selectShipBisEquippment(
      updateJson ? updateJson.ship : shipList[shipIndex].file,
      equippableArr1,
      equippableArr2,
      equippableArr3,
      equippableAuxArr,
      shipHasKai,
      updateJson
    );
  };

  const selectShipBisEquippment = (
    shipName,
    equippableArr1,
    equippableArr2,
    equippableArr3,
    equippableAuxArr,
    shipHasKai,
    updateJson = null
  ) => {
    let tempFeedbackJson = { kai: shipHasKai };
    tempFeedbackJson.ship = shipName;
    tempFeedbackJson.eq1save = false;
    tempFeedbackJson.eq2save = false;
    tempFeedbackJson.eq3save = false;
    tempFeedbackJson.eq4save = false;
    tempFeedbackJson.eq5save = false;
    if (!bisGearTable.mapping[shipName] && updateJson === null) {
      tempFeedbackJson.eq1 = equippableArr1[0].file;
      tempFeedbackJson.eq2 = equippableArr2[0].file;
      tempFeedbackJson.eq3 = equippableArr3[0].file;
      tempFeedbackJson.eq4 = equippableAuxArr[0].file;
      tempFeedbackJson.eq5 = equippableAuxArr[0].file;
      setShipFeedbackJson(tempFeedbackJson);
      updateshipjson(tempFeedbackJson, selectorid);
      return;
    }
    const template = bisGearTable.template[bisGearTable.mapping[shipName]];

    let gear1 = bisGearTable.slot[template.slot_1][0].gear;
    let gear2 = bisGearTable.slot[template.slot_2][0].gear;
    let gear3 = bisGearTable.slot[template.slot_3][0].gear;
    let gear4 = bisGearTable.slot[template.slot_4][0].gear;
    let gear5 = bisGearTable.slot[template.slot_5][0].gear;

    if (updateJson) {
      if (updateJson.eq1) {
        gear1 = updateJson.eq1;
        tempFeedbackJson.eq1save = true;
      }
      if (updateJson.eq2) {
        gear2 = updateJson.eq2;
        tempFeedbackJson.eq2save = true;
      }
      if (updateJson.eq3) {
        gear3 = updateJson.eq3;
        tempFeedbackJson.eq3save = true;
      }
      if (updateJson.eq4) {
        gear4 = updateJson.eq4;
        tempFeedbackJson.eq4save = true;
      }
      if (updateJson.eq5) {
        gear5 = updateJson.eq5;
        tempFeedbackJson.eq5save = true;
      }
    }

    const g1len = equippableArr1.length;
    const g2len = equippableArr2.length;
    const g3len = equippableArr3.length;
    const auxlen = equippableAuxArr.length;
    let g1found = false;
    let g2found = false;
    let g3found = false;
    let aux1found = false;
    let aux2found = false;

    for (let i = 0; i < Math.max(g1len, g2len, g3len, auxlen); i++) {
      if (i < g1len && !g1found) {
        if (equippableArr1[i].file === gear1) {
          g1found = true;
          tempFeedbackJson.eq1 = equippableArr1[i].file;
          setEquipment1(i);
        }
      }

      if (i < g2len && !g2found) {
        if (equippableArr2[i].file === gear2) {
          g2found = true;
          tempFeedbackJson.eq2 = equippableArr2[i].file;
          setEquipment2(i);
        }
      }

      if (i < g3len && !g3found) {
        if (equippableArr3[i].file === gear3) {
          g3found = true;
          tempFeedbackJson.eq3 = equippableArr3[i].file;
          setEquipment3(i);
        }
      }

      if (i < auxlen && !aux1found) {
        if (equippableAuxArr[i].file === gear4) {
          aux1found = true;
          tempFeedbackJson.eq4 = equippableAuxArr[i].file;
          setEquipment4(i);
        }
      }

      if (i < auxlen && !aux2found) {
        if (equippableAuxArr[i].file === gear5) {
          aux2found = true;
          tempFeedbackJson.eq5 = equippableAuxArr[i].file;
          setEquipment5(i);
        }
      }
    }

    if (g1found === false) {
      tempFeedbackJson.eq1 = equippableArr1[0].file;
      setEquipment1(0);
    }
    if (g2found === false) {
      tempFeedbackJson.eq2 = equippableArr2[0].file;
      setEquipment2(0);
    }
    if (g3found === false) {
      tempFeedbackJson.eq3 = equippableArr3[0].file;
      setEquipment3(0);
    }
    if (aux1found === false) {
      tempFeedbackJson.eq4 = equippableAuxArr[0].file;
      setEquipment4(0);
    }
    if (aux2found === false) {
      tempFeedbackJson.eq5 = equippableAuxArr[0].file;
      setEquipment5(0);
    }

    setShipFeedbackJson(tempFeedbackJson);
    updateshipjson(
      tempFeedbackJson,
      updateJson ? updateJson.selectorid : selectorid
    );
  };

  const getEquipmentListFromEquippable = (str, shipHullName, iskai) => {
    const matchArr = str.match(/\(.+on retrofit\)/g);
    if (matchArr) {
      if (iskai) {
        str = matchArr[0].replace("(", "").replace("on retrofit)", "");
      } else {
        str.replace(matchArr[0], "");
      }
    }
    if (str.indexOf("/") === -1) {
      const eqType = returnEquipmentNameFromStr(str);
      if (eqType !== "Auxiliary") return [...eqlist[eqType]];
      return filterAuxByShipType(shipHullName);
    }

    let equipArr = [];
    let eqArr = str.split("/");
    for (let i = 0; i < eqArr.length; i++) {
      equipArr = equipArr.concat(
        JSON.parse(JSON.stringify(eqlist[returnEquipmentNameFromStr(eqArr[i])]))
      );
    }
    for (let i = 0; i < equipArr.length; i++) {
      equipArr[i].value = i;
    }
    return equipArr;
  };

  const filterAuxByShipType = (shipHullName) => {
    const shipHullCode = getHullCodeFromStr(shipHullName);
    let matched_aux_list = [];

    for (let i = 0; i < eqlist.Auxiliary.length; i++) {
      const eq_json = require("../../assets/json/eq_json/" +
        eqlist.Auxiliary[i].file +
        ".json");
      if (eq_json[shipHullCode] === "1" || checkUniversalAux(eq_json)) {
        matched_aux_list.push({
          key: eq_json.Type.toLowerCase() + "_" + matched_aux_list.length,
          value: matched_aux_list.length,
          text: eq_json.Name,
          type: eq_json.Type,
          file: eqlist.Auxiliary[i].file,
          image: {
            avatar: false,
            src: require("../../assets/img/eq_images/" +
              eq_json.index +
              ".png"),
          },
        });
      }
    }
    return matched_aux_list;
  };

  const checkShipKai = (shipIndex, updateJson = null) => {
    const ship_json = updateJson
      ? require("../../assets/json/ship/" + updateJson.ship + ".json")
      : require("../../assets/json/ship/" + shipList[shipIndex].file + ".json");
    if (ship_json.Remodel === "t") {
      setShipHasKai(true);

      if (updateJson) {
        setShipKai(updateJson.kai);
        return updateJson.kai;
      } else {
        setShipKai(true);
        return true;
      }
    } else {
      setShipHasKai(false);
      if (updateJson) {
        setShipKai(updateJson.kai);
        return updateJson.kai;
      } else {
        setShipKai(false);
        return false;
      }
    }
  };

  const returnEquipmentNameFromStr = (str) => {
    if (str.indexOf("DD") > -1) {
      return "DD Gun";
    } else if (str.indexOf("CL") > -1) {
      return "CL Gun";
    } else if (str.indexOf("CA") > -1) {
      return "CA Gun";
    } else if (str.indexOf("CB") > -1) {
      return "CB Gun";
    } else if (str.indexOf("BB") > -1) {
      return "BB Gun";
    } else if (str.indexOf("Anti-Air") > -1) {
      return "AA Gun";
    } else if (str.indexOf("Fighter") > -1) {
      return "Fighter";
    } else if (str.indexOf("Dive Bomber") > -1) {
      return "Dive Bomber";
    } else if (str.indexOf("Torpedo Bomber") > -1) {
      return "Torpedo Bomber";
    } else if (str.indexOf("Submarine Torpedoe") > -1) {
      return "Submarine Torpedo";
    } else if (str.indexOf("Seaplane") > -1) {
      return "Seaplane";
    } else if (str.indexOf("Torpedoe") > -1) {
      return "Torpedo";
    } else if (str.indexOf("Cargoes") > -1) {
      return "Cargo";
    } else {
      return "Auxiliary";
    }
  };

  const getHullCodeFromStr = (str) => {
    switch (str) {
      case "Destroyer":
        return "DD";
      case "Monitor":
        return "BM";
      case "Light Cruiser":
        return "CL";
      case "Heavy Cruiser":
        return "CA";
      case "Aircraft Carrier":
        return "CV";
      case "Repair Ship":
        return "AR";
      case "Battleship":
        return "BB";
      case "Submarine":
        return "SS";
      case "Battlecruiser":
        return "BC";
      case "Large Cruiser":
        return "CB";
      case "Light Aircraft Carrier":
        return "CVL";
      case "Submarine Carrier":
        return "SSV";
      case "Munition Ship":
        return "AE";
      default:
        return "DD";
    }
  };

  const checkUniversalAux = (eq_json) => {
    if (
      eq_json.DD === "" &&
      eq_json.CL === "" &&
      eq_json.CA === "" &&
      eq_json.CB === "" &&
      eq_json.BM === "" &&
      eq_json.BB === "" &&
      eq_json.BC === "" &&
      eq_json.BBV === "" &&
      eq_json.CV === "" &&
      eq_json.CVL === "" &&
      eq_json.AR === "" &&
      eq_json.SS === "" &&
      eq_json.SSV === ""
    ) {
      return true;
    }
    return false;
  };

  const updateShipFeedbackJson = (val, prop, externalJson = null) => {
    let tempJson = externalJson ? { ...externalJson } : { ...shipFeedbackJson };
    tempJson[prop] = val;
    setShipFeedbackJson(tempJson);
    return tempJson;
  };

  return shipList.length === 0 ? (
    ""
  ) : (
    <Col md="4">
      <h5>{props.shiprole}</h5>
      <Dropdown
        style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
        className={theme ? "semanticDropdownDark" : ""}
        fluid
        selection
        labeled
        search
        closeOnChange
        options={shipList}
        value={shipValue}
        onChange={(event, data) => {
          setShipValue(data.value);
          setShipEquipment(data.value);
          if (data.value === 0) {
            setEquipment1List([]);
            setEquipment2List([]);
            setEquipment3List([]);
            setEquipment4List([]);
            setEquipment5List([]);
            setShipHasKai(false);
            updateshipjson(null, selectorid);
          }
        }}
      />
      {shipHasKai ? (
        <>
          <Checkbox
            label={
              <label>
                <span style={theme ? { color: "#FFFFFF" } : {}}>Kai</span>
              </label>
            }
            checked={shipKai}
            onChange={(event, data) => {
              setShipKai(data.checked);
              toggleKai(data.checked === true);
            }}
            toggle
          />
        </>
      ) : (
        ""
      )}
      {equipment1List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment1List}
          value={equipment1}
          onChange={(event, data) => {
            setEquipment1(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq1"
            );
            tempjson = updateShipFeedbackJson(true, "eq1save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment2List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment2List}
          value={equipment2}
          onChange={(event, data) => {
            setEquipment2(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq2"
            );
            tempjson = updateShipFeedbackJson(true, "eq2save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment3List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment3List}
          value={equipment3}
          onChange={(event, data) => {
            setEquipment3(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq3"
            );
            tempjson = updateShipFeedbackJson(true, "eq3save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment4List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment4List}
          value={equipment4}
          onChange={(event, data) => {
            setEquipment4(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq4"
            );
            tempjson = updateShipFeedbackJson(true, "eq4save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment5List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          style={theme ? { backgroundColor: "#2F3136", color: "#FFF" } : {}}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment5List}
          value={equipment5}
          onChange={(event, data) => {
            setEquipment5(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq5"
            );
            tempjson = updateShipFeedbackJson(true, "eq5save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
    </Col>
  );
});

export default ShipSelector;
