import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";

import "assets/css/bootstrap.min.css";
import "assets/scss/now-ui-kit.scss?v=1.4.0";
import "assets/demo/demo.css?v=1.4.0";
import "assets/demo/nucleo-icons-page-styles.css?v=1.4.0";
import Index from "views/Index.js";
import Calculator from "views/Calculator.js";
import Newbguide from "views/Newbguide.js";
import Pvpguide from "views/Pvpguide.js";
import Fleetmaker from "views/Fleetmaker.js";
import Contributors from "views/Contributors.js";
import Changelogs from "views/Changelogs.js";
import Guidelines from "views/Guidelines.js";

ReactDOM.render(
  <HashRouter>
    <Switch>
      <Switch>
        <Route path="/home" render={(props) => <Index {...props} />} />
        <Route
          path="/calculator"
          render={(props) => <Calculator {...props} />}
        />
        <Route path="/newbguide" render={(props) => <Newbguide {...props} />} />
        <Route path="/pvpguide" render={(props) => <Pvpguide {...props} />} />
        <Route
          path="/fleetmaker"
          render={(props) => <Fleetmaker {...props} />}
        />
        <Route
          path="/contributors"
          render={(props) => <Contributors {...props} />}
        />
        <Route
          path="/changelogs"
          render={(props) => <Changelogs {...props} />}
        />
        <Route
          path="/guidelines"
          render={(props) => <Guidelines {...props} />}
        />
        <Redirect to="/home" />
        <Redirect from="/" to="/home" />
      </Switch>
    </Switch>
  </HashRouter>,
  document.getElementById("root")
);

