import React from "react";

import IndexNavbar from "components/Navbars/IndexNavbar.js";
import IndexHeader from "components/Headers/IndexHeader.js";
import DarkFooter from "components/Footers/DarkFooter.js";

import TierList from "components/TierList/TierList.js";

function Index() {
  const [theme] = React.useState(localStorage.getItem("theme") === "true");

  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <IndexNavbar />
      <div
        className="wrapper"
        style={theme ? { backgroundColor: "#000000AA" } : {}}
      >
        <IndexHeader />
        <div className="main">
          <a
            href="https://discord.gg/5mRxncu"
            target="_blank"
            rel="noopener noreferrer"
          >
            <div
              className="discordBarStyle"
              style={
                theme
                  ? { backgroundColor: "#2F3136" }
                  : { backgroundColor: "#444" }
              }
            >
              <i className="fab fa-discord"></i> Click here to join our Discord
              server!
            </div>
          </a>

          <TierList />
        </div>
        <DarkFooter />
      </div>
    </>
  );
}

export default Index;
